.short-cont {
  border: 4px solid #966020;
  margin: 0 auto;
  margin-top: 4em;
  width: 100%;
  max-width: 992px;
  color: white;
}

.short-title {
  width: 100%;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 900;
  font-size: 3rem;
  display: flex;
  align-items: center;

  justify-content: center;

  color: #ffffff;
  border-bottom: 4px solid #966020;
}

.short-title img {
  margin-left: 0.5em;
  width: 48px;
}
.short-main {
  overflow-x: hidden;
}

#idk {
  display: flex;
  position: relative;
}

.more {
  margin-left: 2em;
  height: 50px;
  background-color: #966020;
  color: #0e0904;
  align-self: center;
  font-family: "Lato";
  font-style: normal;
  font-weight: 700;
  font-size: 1rem;
  border: none;
}

.el-cont {
  min-width: 30%;
  border: 2px solid #966020;
  text-align: center;
}

.el-cont:hover {
  background-color: #ca842e50;
}

.el-img img {
  height: 200px;
  padding: 1em 0;
}

.el-price {
  border-top: 2px solid #966020;
}
.el-price h3 {
  font-family: "Lato";
  font-style: normal;
  font-weight: 700;
  font-size: 1.5rem;
  margin: 0;
  margin-top: 0.5em;
}

.el-price h5 {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  margin-top: 0.5em;
  color: #ffffff;
}

.right {
  width: 75px;
  display: block;
  z-index: 1;
}

.left {
  width: 75px;
  transform: rotate(180deg);
  z-index: 1;
  display: none;
}

#arrows {
  max-width: 990px;
  width: 60%;
  height: 250px;
  position: absolute;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

@media only screen and (max-width: 1500px) {
  .el-img img {
    height: 175px;
  }
}

@media only screen and (max-width: 1345px) {
  .el-img img {
    height: 150px;
  }
  .short-title {
    font-size: 2rem;
  }
  .el-price h3 {
    font-size: 1rem;
  }
  .el-price h5 {
    font-size: 0.7rem;
  }
}
