.header {
  background: #0e0903;
  border-bottom: 1px solid #966020;
  height: 10vh;
}
.field {
  width: 60%;
  max-width: 1000px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo {
  height: 60px;
}

.list {
  font-family: "Lato";
  list-style: none;
  font-weight: 400;
  font-size: 1.5rem;
  display: flex;
  color: #ffffff;
  margin: 0;
}
.link {
  color: white;
  text-decoration: none;
}

.list li {
  margin-left: 2em;
}
