.shopping-container {
  color: white;
  width: 50%;
  margin: auto;
  margin-top: 2em;
  background-color: #251808;

  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.shopping-img {
  height: 200px;
  padding: 1em 0;
  margin: 0 2em;
}

.kj {
  display: none;
}

.shopping-box {
  display: flex;
  margin: 1em;
  padding-top: 1em;
  background-color: #140d04;
  border: 4px solid #966020;
}

.cena {
  text-align: end;
  margin-right: 2em;
}

.buy {
  width: 150px;
  height: 75px;
  background-color: #966020;
  border: none;
  font-size: 1rem;
  color: white;
  margin: 1em;
}
.buy:hover {
  background-color: #744b19;
}

.back {
  position: fixed;
  width: 150px;
  z-index: 2;
  left: 150px;
  bottom: 150px;
}

.thanks {
  text-align: center;
  color: white;
}
