.main-cont {
  width: 60%;
  max-width: 1000px;
  margin: 0 auto;
  margin-top: 2em;
}

.banner {
  width: 100%;
}

.cart {
  position: fixed;
  width: 150px;
  z-index: 2;
  right: 150px;
  bottom: 75px;
}
.cart:hover {
  width: 175px;
}

.count {
  color: white;
  position: fixed;
  font-size: 5rem;
  z-index: 2;
  right: 150px;
  bottom: 75px;
  font-family: "Lato";
  font-weight: 700;
}

@media only screen and (max-width: 1500px) {
  .cart {
    right: 100px;
  }
  .count {
    right: 100px;
  }
}
