.foot-cont {
  margin-top: 3em;
  background: #0e0903;
  border-top: 1px solid #966020;
  height: 5vh;
}

.foot-content {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 60%;
  max-width: 1000px;
  margin: auto;
}

.foot-content a {
  color: #ddd;
  text-decoration: none;
}
